.page{
    .sidebar{
        position: fixed;
        left: 0;
        top: 0;
        width: 250px;
        height: 100%;
        background-color: #eee;
        padding: 15px;
        transition: .3s;
        margin-right: 10px;
        &.no-sidebar{
            left: -250px;
        }
    }
}
